import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
import Server_connections from '../../constants/constants';
// import Spinner from '../../Loaders/Spinner'
// import '../ProductList/ShopStylee.css'

class AddChargeModal extends Component {
    constructor(props) {
      super(props);
      this.state = {
        amount: '',
        message: '',
        isLoaded: false
      }
      this.handleUpdateItem = this.handleUpdateItem.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    handleUpdateItem(e){
      e.preventDefault();

      const addNewAmount = {   
        amount: this.state.amount,
      };

      console.log(addNewAmount);

      fetch(Server_connections.ADD_NEW_CHARGES_URL,{
        method: 'POST',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(addNewAmount)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });

      var { isLoaded } = this.state;

    }
    
    render() {
      var { isLoaded, handleUpdateItem} = this.state;
      
        return (
            <Modal {...this.props} size="sm" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter"><h5>Add New Amount</h5></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h5><span className='modal-alert'>{this.state.message }</span></h5>
                    <FormGroup row className="my-0">
                        <Col xs="12">
                            <FormGroup>
                                {/* <Label htmlFor="product-barcode" className="lable">Product Ref (GTIN)</Label> */}
                                <Input type="select" name="amount" value={this.state.state} onChange={this.onChange}>
                                    <option>Select-amount</option>
                                    <option></option>
                                    <option value={this.state.state}>1000</option>
                                    <option value={this.state.state}>1500</option>
                                    <option value={this.state.state}>2000</option>
                                    <option value={this.state.state}>2500</option>
                                    <option value={this.state.state}>3000</option>
                                    <option value={this.state.state}>3500</option>
                                    <option value={this.state.state}>4000</option>
                                    <option value={this.state.state}>4500</option>
                                    <option value={this.state.state}>5000</option>
                                    <option value={this.state.state}>5500</option>
                                    <option value={this.state.state}>6000</option>
                                </Input>
                            </FormGroup>
                        </Col>
                    </FormGroup>
                </Modal.Body>
                <Modal.Footer>
                <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Close</Button>
                </div>
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleUpdateItem}>Save</Button>
                </div>
              </Modal.Footer>
            </Modal>
        )
    }
}

export default AddChargeModal;
